import React from "react";
import axios from 'axios'
import './ContactForm.css'

class ContactForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      email: '',
      message: ''
    }
  }

  handleSubmit(e){
    e.preventDefault();
    axios({
      method: "POST",
      url:"http://hebronhigh.github.io/hawkfest/contact/send",
      data:  this.state
    }).then((response)=>{
      if (response.data.status === 'success') {
        alert("Message Sent.");
        this.resetForm()
      } else if (response.data.status === 'fail') {
        alert("Message failed to send.")
      }
    })
  }

  resetForm(){
    this.setState({name: '', email: '', message: ''})
  }

  render() {
    return (
      <form onSubmit={this.handleSubmit.bind(this)}><br></br>
        <div>
          <label htmlFor="name">Name:</label>
          <br></br>
          <textarea type="text" id="name" class="name" value={this.state.name} onChange={this.onNameChange.bind(this)} required />
        </div>
        <div>
          <label htmlFor="email">Email:</label><br></br>
          <textarea type="email" id="email" class="email" value={this.state.email} onChange={this.onEmailChange.bind(this)} required />
        </div>
        <div>
          <label htmlFor="message">Message:</label><br></br>
          <textarea id="message" value={this.state.message} onChange={this.onMessageChange.bind(this)} required />
        </div>
        <button type="submit">Submit</button>
      </form>
    );
  }

  onNameChange(event) {
    this.setState({name: event.target.value})
  }

  onEmailChange(event) {
    this.setState({email: event.target.value})
  }

  onMessageChange(event) {
    this.setState({message: event.target.value})
  }
};

export default ContactForm;